import { useQuery } from "@tanstack/react-query";
import {
  getAllCoupons,
  getAllDealOfTheDay,
  getAllFeaturedDeals,
  getAllFlashDeals,
  getFeaturedDealById,
  getFlashDealById,
} from "../api/dealApis";

const useCoupons = () => {
  return useQuery({
    queryKey: ["coupons"],
    queryFn: getAllCoupons,
    staleTime: 1000 * 60 * 5,
    placeholderData: [],
  });
};

const useDeal = (dealName = "flash", dealId) => {
  return useQuery({
    queryKey: [dealName, dealId],
    queryFn:
      dealName === "flash"
        ? () => getFlashDealById(dealId)
        : dealName === "featured"
        ? () => getFeaturedDealById(dealId)
        : null,
    staleTime: 1000 * 60 * 5,
    enabled: !!dealId,
  });
};

const useFlashDeals = (options) => {
  return useQuery({
    queryKey: ["flashDeals"],
    queryFn: getAllFlashDeals,
    staleTime: 1000 * 60 * 5,
    ...options,
  });
};

const useFeaturedDeals = (options) => {
  return useQuery({
    queryKey: ["featuredDeals"],
    queryFn: getAllFeaturedDeals,
    staleTime: 1000 * 60 * 5,
    ...options,
  });
};

const useDealsOfTheDay = (options) => {
  return useQuery({
    queryKey: ["dealsOfTheDay"],
    queryFn: getAllDealOfTheDay,
    staleTime: 1000 * 60 * 5,
    ...options,
  });
};

export {
  useCoupons,
  useDeal,
  useFlashDeals,
  useFeaturedDeals,
  useDealsOfTheDay,
};
