import { useParams } from "react-router-dom";
import HeroBanner from "../../components/HeroBanner";
import DealProducts from "./DealProducts";
import Loader from "../../components/Loader";
import { useDeal } from "../../services/dealServices";

const Deal = ({ dealName = "flash" }) => {
  const { dealId } = useParams();
  const { data: deal, isLoading, error } = useDeal(dealName, dealId);

  if (isLoading || (!error && !deal)) {
    return <Loader />;
  }

  if (error) {
    return (
      <div className="w-max mx-auto p-8 bg-red-300 my-8 rounded-lg border-2 border-error">
        <h2 className="text-center text-error font-medium text-xl">
          Deal has expired
        </h2>
      </div>
    );
  }

  return (
    <main className="py-8 space-y-8">
      <HeroBanner
        position="Top"
        resourceType={dealName === "flash" ? "Flash Deal" : "Featured Deal"}
        valueId={dealId}
      />
      <DealProducts products={deal?.products} title={deal?.title} />
    </main>
  );
};

export default Deal;
