import { useMemo } from "react";
import Pagination from "../../components/Pagination";
import ProductBox from "../../components/ProductBox";
import { useSearchParamsState } from "../../hooks/useSearchParamsState";

const DealProducts = ({ products = [], title }) => {
  const [page, setPage] = useSearchParamsState("page", 1);
  const limit = 20;

  const currentProducts = useMemo(() => {
    const firstPageIndex = (page - 1) * limit;
    const lastPageIndex = firstPageIndex + limit;
    return products.slice(firstPageIndex, lastPageIndex);
  }, [page, products, limit]);

  return (
    <div className="container max-w-full space-y-8 max-lg:space-y-4">
      <h1 className="text-2xl max-lg:text-xl font-bold">{title}</h1>

      <section className="w-full">
        {products.length === 0 ? (
          <p className="text-center font-secondary font-medium text-xl">
            No Products Found
          </p>
        ) : (
          <ul className="grid grid-cols-2 lg:gap-8 sm:gap-4 gap-1 sm:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5">
            {currentProducts.map((productItem, index) => (
              <li key={index} className="flex justify-center items-center">
                <ProductBox product={productItem} />
              </li>
            ))}
          </ul>
        )}

        <div className="PAGINATION">
          <Pagination
            currentPage={parseInt(page, 10)}
            totalCount={products.length}
            pageSize={limit}
            onPageChange={(page) => setPage(page.toString())}
          />
        </div>
      </section>
    </div>
  );
};

export default DealProducts;
